@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@300;400;600;700&display=swap');
@import url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

@import url("defaults.css");
@import url("tooltip.css");

/* Variables */

$yldGrey: #181b1e;
$yldGold: #efbc86;

$black: #121212;
$highlightBlue: #3b88fd;
$lightGrey: #ebebeb;

$ptMono: 'Inter', sans-serif;

$gradBgDark: linear-gradient(145deg, rgba(#000,0.25), rgba(#000,0.1));
$gradBgLight: linear-gradient(145deg, rgba(#ccc,1), rgba(#fff,1));

$shadowLight: rgba(#fff,0.05);
$shadowDark: rgba(#000,0.5);
$btnTransition: box-shadow 0.2s ease-out, border-color 0.5s ease-out 0.1s;
$btnBrdBtm: 1px solid rgba($yldGold,0.25);
$btnBrdBtmAlt: 1px solid rgba(#fff,0.25);

/* End Variables */

/* Mixins */

@mixin heading2 {
  margin: 0 0 0 0;
  grid-column: span 3;
  font-weight: 600;
  font-size: 1.5rem;
  color: rgba($yldGold,1);
  background: $gradBgDark;
  padding: 1rem;
  border-radius: 0;
  text-align: center;
}

@mixin softFlat($size, $shade){

  @if $shade == 'light' {
    background: $gradBgLight;
  } @else {
    background: $gradBgDark;
  }

  border-radius: ($size*2)+rem;
  box-shadow: ($size*0.5)+rem ($size*0.5)+rem ($size*1.25)+rem $shadowDark,
           ($size*(-0.5))+rem ($size*(-0.5))+rem ($size*1.25)+rem $shadowLight;
}

@mixin softIn($size, $shade){

  @if $shade == 'light' {
    background: $gradBgLight;
  } @else {
    background: $gradBgDark;
  }

  border-radius: ($size*2)+rem;
  box-shadow: inset ($size*0.5)+rem ($size*0.5)+rem ($size*1.25)+rem $shadowDark,
           inset ($size*(-0.5))+rem ($size*(-0.5))+rem ($size*1.25)+rem $shadowLight;
}

/* End Mixins */

html, body {
  background: rgb(15,15,15);
  background: linear-gradient(0deg, rgba(15,15,15,1) 0%, rgba(24,24,24,1) 100%);

  font-size: 12px;

  @media screen and (max-width: 576px){
    font-size: 10px;
  }
}

body {
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-stroke: 0px !important;
  font-family: $ptMono;
  color: #fff;
}

.glow {
    position: fixed;
    top: 25vh;
    left: 0;
    width: 100%;
    height: 30vh;
    z-index: 0;
    // display: none;

    .circle {
        width: 100%;
        // transform: translateX(-10%);
        height: 100%;
        background: #fff;
        filter: blur(10rem) opacity(0.05);
    }
}

main {
  overflow: hidden;
  position: relative;
  z-index: 1;
}

header {
  text-align: center;
  padding: 1rem;

  h1 {
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      max-width: 2rem;
      box-shadow: 0 0 8px rgba(lighten($yldGold,7.5%),0.3125);
      border-radius: 50%;
    }

    span {
      color: $yldGold;
      text-shadow: 0 0 8px rgba(lighten($yldGold,7.5%),0.3125);
      display: inline-block;
      margin: 0 0.5rem;
    }
  }
}

section {
  padding: 0 2rem 2rem 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

.master-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-column-gap: 4rem;
  grid-row-gap: 4rem;
}



.fieldset-wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-size: 1.25rem;
  grid-column: span 3;
  @include softFlat(1,dark);

  h2 {
    @include heading2;
  }

  fieldset {
    padding: 1em;
    border: none;
    margin: 0;

    &.grid {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 1.5rem 2rem;

      @media screen and (max-width: 768px){
        grid-template-columns: 1fr;

        .input-group-parent {
          grid-column: 1 / span 6 !important;
        }
      }
    }

    label {
      margin-bottom: 0.5rem;
      font-weight: 600;
      text-transform: uppercase;
      font-size: 1.125rem;
      letter-spacing: 0.1rem;
      color: #ddd;

      &:not(:first-of-type){
        margin-top: 2rem;
      }

      span {
        color: $yldGold;
      }
    }

    input[type="submit"]{
      background: $yldGold;
      padding: 1rem;
      grid-row: 3;

      &:hover,
      &:focus {
        background: lighten($yldGold,5%);
      }
    }

    .input-group-parent {
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;

      > *:not(.tooltip) {
        max-width: 500px;
        width: 100%;
      }

      .input-group {
        display: flex;
        align-items: center;
        padding: 0.5rem 0;

        input[type="number"]{
          width: 6rem;
          border-radius: 1rem;
          border: none;
          text-align: center;
          font-size: 1.25rem;
          font-weight: 700;
          color: #fff;
          padding: 0.5rem;
          @include softFlat(0.5,dark);
        }

        [id*="flat-slider"]{
          width: calc(100% - 120px);
          margin-top: 1.25rem;
        }

        input.inputRange {
          // width: calc(100% - 120px);
          // width: 0;
          // height: 0;
          // display: block;
          // visibility: hidden;
        }
      }

      // &:nth-of-type(1) {
      //   grid-column: 1 / span 3;
      // }
      //
      // &:nth-of-type(2) {
      //   grid-column: 4 / 7;
      // }
      //
      // &:nth-of-type(3) {
      //   grid-column: 1 / span 3;
      // }
      //
      // &:nth-of-type(4) {
      //   grid-column: 4 / 7;
      // }
      //
      // &:nth-of-type(5) {
      //   grid-column: 1 / span 3;
      // }
      //
      // &:nth-of-type(6) {
      //   grid-column: 4 / 7;
      // }
      //
      // &:nth-of-type(7) {
      //   grid-column: 4 / 6;
      // }
      //
      // &:nth-of-type(7) {
      //   grid-column: 4 / 6;
      // }
      //
      // &:nth-of-type(7) {
      //   grid-column: 4 / 6;
      // }


      &:nth-of-type(n+3){
        .input-group {
          input[type="number"]{
            max-width: 4rem;
          }
          input.inputRange {
            width: calc(100% - 4rem);
          }
        }
      }
    }

    .theoretical {
      grid-column: 2 / 6;

      > div {
        display: grid;
        grid-template-columns: 1fr 1fr;

        h3 {
          margin: 0 0 0 0;
          grid-column: span 3;
          font-weight: 600;
          font-size: 1.05rem;
          background: lighten($yldGrey,10%);
          color: rgba(#fff,1);
          padding: 0.5rem 0.5rem;
          border-radius: 0;
          text-align: center;
          grid-column: 1 / 2;
        }
      }
    }
  }
}

.loan-numbers {
  box-sizing: border-box;
  grid-column: span 3;
  text-align: center;
  overflow: hidden;
  z-index: 1;
  @include softFlat(1,dark);

  h2 {
    @include heading2;
  }

  .outputs {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2rem;
    padding: 2rem 2rem;

    @media screen and (max-width: 640px){
      grid-template-columns: 1fr 1fr;
    }
    @media screen and (max-width: 420px){
      grid-template-columns: 1fr;
    }



    h3 {
      margin-bottom: 1rem;
      font-size: 1.125rem;
      font-weight: 400;
      text-transform: uppercase;
      font-weight: 600;
      letter-spacing: 0.1rem;
      color: #ddd;

      span.fa {
        color: #ddd;
        margin-left: 0.5rem;
      }

      &:first-of-type {
        margin-top: 0;
      }
    }

    p {
      color: $yldGold;
      margin: 0;
      font-size: 1.75rem;
      font-weight: 600;
      text-shadow: 0 0 5px rgba(lighten($yldGold,7.5%),0.25);
      display: inline-block;
      padding: 0.5rem 1.5rem;
      transition: all 0.2s ease-out;
      @include softFlat(0.75,dark);

      &:hover,
      &:focus {
        color: lighten($yldGold,2.5%);
        text-shadow: 0 0 5px rgba(lighten($yldGold,10%),0.5);
      }

      &.dollars {
        &:before {
          content: '$';
          display: inline;
        }
      }
    }
  }
}

.chart-wrapper {
  grid-column-end: span 3;
  position: relative;
  z-index: 0;

  .inside {
    max-width: 1000px;
    height: 300px;
    margin: 0 auto;
  }

  a.switch-chart {
    color: $yldGold;
    font-size: 1.25rem;
    margin-bottom: 1rem;
    display: inline-block;
    text-decoration: none;
    transition: all 0.2s ease-out;

    span.fa {
      transition: all 0.6s ease-in-out;
    }

    &:hover,
    &:focus {
      color: lighten($yldGold,7.5%);
      text-shadow: 0 0 8px rgba(lighten($yldGold,7.5%),0.25);

      span.fa {
        transform: rotate(270deg);
      }
    }
  }

  #supply {
    position: relative;
    z-index: 1;
  }
}

/*tooltip Box*/
.con-tooltip {
  position: relative;
  display: inline-block;
  transition: all 0.3s ease-in-out;
  cursor: default;
}

/* Modal */
.modal {
  color: lighten($yldGrey,5%);

  .modal-header {
    padding: 2rem;

    .modal-title {
      font-size: 2rem;
    }

    button.close {
      font-size: 3rem;
    }
  }

  .modal-content {
    background: #eee;
    border: 1px solid #555;
    box-shadow: 0 0 1rem rgba(lighten($yldGold,10%),0.125);

    .modal-body {
      font-size: 1.5rem;
      line-height: 1.5;
      padding: 2rem;

      p {
        &:first-child {
          margin-top: 0;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
      q {
        font-style: italic;
        font-weight: 600;
      }
    }
  }
}

